<template>
  <form class="card auth-card" @submit.prevent="">
    <div class="card-content">
    <img class="logo-image" :style="''" src="@/assets/newImages/logo.png">
    <p class="reset-password"> {{ $localizeFilter('reset_password') }} </p>
      
      <div class="input-field">
        <label for="email"
          >{{ $localizeFilter('email') }}<span class="required">*</span></label
        >
        <input
          id="email"
          type="text"
          v-model.trim="email"
          :class="{ invalid: v$.email.$error }"
        />
        <small
          class="error"
          v-for="(error, index) of v$.email.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
    </div>

    <div class="card-action">
      <div>
        <button class="button-2" @click.once="submitHandler" :key="buttonKey">
          <p class="button-title">{{ $localizeFilter('reset_password_button')}}</p>
          <i class="material-icons right">send</i>
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, email, helpers} from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from "@/filters/localize.filter"

export default defineComponent({
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      buttonKey: 1
    };
  },
  validations() {
    return {
       email: {
       required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
       email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      },
    };
  },
  methods: {
    async submitHandler() {
      this.v$.$touch();
        if (this.v$.$error) {
          this.buttonKey+=1
          return;
        }
        try {
          await this.$store.dispatch('requestRestorePassword', {email: this.email})
          this.$message(localizeFilter('restore_password_link_sent'))
          this.$router.push('/login')
        } catch (e) {
             this.$error(e)
             this.buttonKey+=1
        }
    },
  },
  computed: {
  },
});
</script>

